.advantages{
  background-color: var(--grey-color);
  padding-bottom: 100px;
  &__top{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 70px;
    @include large-tablet{
      flex-direction: column;
      margin-bottom: 45px;
    }
  }
  &__title{
    flex: 0 1 50%;
    max-width: 406px;
    @include large-tablet{
      margin-bottom: 25px;
    }
  }
  &__title--bottom{
    margin-top: 100px;
    margin-bottom: 75px;
    @include large-tablet{
      margin-top: 75px;
      margin-bottom: 45px;
    }
  }
  &__text{
    flex: 0 1 50%;
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 16px;
    line-height: 130%;
    @include large-tablet{
      margin-bottom: 35px;
    }
  }
  &__links{
    order: 3;
  }
  &-numbers{
    &__list{
      display: flex;
      justify-content: space-between;
      @include tablet{
        display: inline-flex;
        flex-direction: column;
      }
    }
    &__item{
      padding-bottom: 10px;
      flex: 0 1 33%;
      border-bottom: 2px solid var(--dark-color);
    }
    &__item:not(:last-child){
      @include tablet{
        margin-bottom: 25px;
      }
    }
    &__number{
      font-weight: 400;
      font-size: 48px;
      line-height: 120%;
      margin-bottom: 10px;
    }
    &__descr{
      font-weight: 500;
      font-size: 18px;
      line-height: 120%;
    }
  }
  &-conditions{
    &__list {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      row-gap: 20px;
    }
    &__item{
      flex: 0 1 33%;
      @include large-tablet{
        flex: 0 1 49%;
      }
      @include mobile{
        flex: 0 1 100%;
      }
    }
    &__item img{
      margin-bottom: 20px;
    }
  }
}
.links{
  margin-bottom: 25px;
  &__videos{
    display: flex;
    align-items: center;
  }
  &__comments{
    display: flex;
    align-items: center;
  }
}
.comments{
  margin-top: 15px;
  &__link{
    margin-left: 10px;
  }
}
.videos{
  &__img{
    position: relative;
    width: 25px;
    height: 25px;
    border: 1px solid var(--akcent-color);
    border-radius: 100%;
    margin-right: 10px;
  }
  &__img img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-40%);
    transform: translateY(-50%);
  }
}