.header{
  padding-top: 57px;
  padding-bottom: 15px;
  @include desktop{
    padding-top: 25px;
    padding-bottom: 10px;
  }
  @include large-tablet {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &__main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
  }
  &__img{
    background: url('/img/logo.jpg');
    @include bg-cover;
    width: 195px;
    height: 45px;
    @include large-tablet{
      background: url('/img/logo_mini.png');
      @include bg-cover;
      width: 100px;
      height: 30px;
      margin-bottom: 10px;
    }
  }
  &-nav{
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1000;
    background: var(--burger_menu-color);
    height: 400px; 
    transform: translateX(100%);
    transition: transform .6s ease-in-out;
    &__list{
      padding-top: 60px; 
      padding-bottom: 20px;                
    }
    &__item{
      padding: 20px 20px;   
    }
    &__link{
      color: var(--light-color); 
    }
  }
  &__langs{
    @include large-tablet{
      border-bottom: 1px solid var(--grey-color);
      padding-bottom: 25px;
    }
  }
  &__link{
    text-transform: uppercase;
    color: var(--light-color)
  }
  &__link--ru{
    margin-right: 15px;
    text-decoration: underline;
  }
}
.burger{
  z-index: 10000;
  display: none;
  @include burger;
  @include large-tablet{
    display: block;
  }
}
.menu--active{
  transform: none;
}

.page__body{
  overflow-x: hidden;
}