.clients{
  &__title{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    max-width: 387px;
    margin-bottom: 60px;
    @include large-tablet{
      margin-bottom: 30px;
    }
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    @include tablet{
      grid-template-columns: repeat(2, 1fr);
    }
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex: 0 1 25%;
    // padding: 20px 50px;
    max-width: 255px;
    max-height: 150px;
    background-color: var(--grey-color);
    // @include desktop{
    //   max-width: 240px;
    //   max-height: 135px;
    // }
    // @include large-tablet{
    //   max-width: 220px;
    //   max-height: 120px;
    // }
  }
}