.directions{
  padding-top: 100px;
  padding-bottom: 100px;
  @include large-tablet{
    padding-top: 40px;
    padding-bottom: 40px;
  }
  &__title{
    max-width: 560px;
    margin-bottom: 35px;
    @include large-tablet{
      margin-bottom: 25px;
    }
  }
  &__text{
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
    @include large-tablet{
      flex-direction: column;
      margin-bottom: 45px;
    }
  }
  &__text:first-child{
    max-width: 440px;
    margin-left: 147px;
    @include large-tablet{
      margin-bottom: 20px;
    }
  }
  &__descr {
    flex: 0 1 50%;
  }
  &__list{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-content: center;
    gap: 40px;
    margin-bottom: 35px;
    @include desktop{
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 25px;
      
    }
    @include small-tablet{
      grid-template-columns: 1fr;
      grid-gap: 15px;
    }
  }
  &__item {
    background-color: var(--grey-color);
    padding: 37px 70px;
  }
  &__item image {
    margin-bottom: 15px;
    border-radius: 100%;
  }
  &__subtitle{
    max-width: 185px;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 35px;
    @include large-tablet{
      font-size: 18px;
    }
  }
  &__links{
    display: flex;
    align-items: baseline;
  }
  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    margin-right: 15px;
    @include large-tablet{
      font-size: 14px;
    }
  }
}