.license{
  padding-bottom: 100px;
  @include large-tablet{
    padding-bottom: 50px;
  }
  &__title{
    max-width: 430px;
    margin-bottom: 60px;
    @include large-tablet{
      margin-bottom: 45px;
    }
    &--bottom{
      margin-bottom: 35px;
    }
  }
  &__images{
    display: flex;
    justify-content: space-between;
    @include small-tablet{
      flex-wrap: wrap;
    }
  }
  &__image{
    @include desktop{
      max-width: 320px;
      max-height: 450px;
    }
    @include large-tablet{
      max-width: 280px;
      max-height: 350px;
    }
    @include tablet{
      max-width: 240px;
      max-height: 340px;
    }
    @include small-tablet{
      flex-direction: column;
    }
    @include small-tablet{
      flex-wrap: wrap;
      flex: 0 1 49%;

    }
  }
  &__text{
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include large-tablet{
      display: block;
    }
  }
  &__descr{
    &--top{
      @include large-tablet{
        margin-bottom: 20px;
      }
    }
  }
}