.blog{
  padding-bottom: 100px;
  @include tablet {
    padding-bottom: 50px;
  }
  &__title{
    max-width: 500px;
    margin-bottom: 75px;
    @include small-tablet{
      margin-bottom: 35px;
    }
  }
  &__list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 35px;
  }
  &__item{
    flex: 0 1 49%;
    border-top: 1px solid var(--grey-color);
    padding-top: 30px;
    @include mobile{
      flex: 0 1 100%;
    }
  }
}
.blog-article{
  max-width: 550px;
  &__title{
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 35px;
  }
  &__info{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 45px;
    @include small-tablet{
      margin-bottom: 20px;
    }
  }
  &__date{
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
  }
}