.about{
  padding-bottom: 100px;
  @include large-tablet{
    padding-bottom: 50px;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 100px;
    @include large-tablet{
      flex-direction: column;
      margin-bottom: 75px;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    @include large-tablet{
      margin: 0;
      flex-direction: column;
    }
  }
  &__title{
    margin-top: -20px;
    flex: 0 1 50%;
    @include large-tablet{
      margin-bottom: 25px;
    }
  }
  &__title--second{
    margin-top: -20px;
    flex: 0 1 50%;
    margin-bottom: 35px;
    @include large-tablet{
      margin-bottom: 25px;
    }
  }
  &__descr{
    flex: 0 1 50%;
    max-width: 480px;
    @include large-tablet{
      max-width: 900px;
    }
    &--first{
      @include large-tablet{
        order: 2;
        margin-top: 20px;
      }
    }
    &--last{
      @include large-tablet{
        order: 3;
        margin-top: 20px;
      }
    }
  }
  &__descr--last{
    margin-top: 28px;
  }
  &__btn{
    margin-top: -50px;
    @include large-tablet{
      margin-top: 35px;
    }
    &--second{
      @include large-tablet{
        order: 5;
      }
    }
  }
  &__btn {
    &--second{
      margin-top: 35px;
      order: 5;
      // margin-top: 35px;
    }
  }
}