.case{
  padding-bottom: 100px;
  background-color: var(--grey-color);
  @include large-tablet{
    padding-bottom: 50px;
  }
  &__title{
    max-width: 360px;
    margin-bottom: 60px;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
  }
  &__subtitle{
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 45px;
    max-width: 509px;
  }

}
.info{
  &__list{
    display: flex;
    margin-bottom: 50px;
    @include mobile {
      margin-bottom: 30px;
    }
    @include mobile{
      display: block;
    }
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__item:not(:last-child){
    margin-right: 30px;
    @include mobile{
      margin-bottom: 15px;
    }
  }
  &__img {
    margin-right: 15px;
  }
  &__descr{
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
  }
}
.case-bottom{
  display: flex;
  justify-content: space-between;
  @include tablet{
    display: block;
  }
  &__title{
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    margin-bottom: 10px;
    text-transform: uppercase;
  }
  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
    margin-bottom: 35px;
    max-width: 530px;
  }
  &__list{
    list-style-type: decimal;
    margin-bottom: 30px;
    @include tablet{
      padding-left: 15px;
    }
  }
  &__text{
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
  }
  &__text--bottom{
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
    margin-bottom: 35px;
  }
  &__btns{
    display: inline-flex;
    flex-direction: column;
  }
  &__left{
    flex: 0 1 50%;
    @include desktop{
      flex: 0 1 47%;
    }
  }
  &__right{
    flex: 0 1 50%;
    @include desktop{
      flex: 0 1 47%;
    }
  }
  &__image{
    @include tablet{
      width: 100%;
      height: 100%;
      margin-bottom: 15px;
    }
  }
}
.btn-2{
  border: 2px solid var(--dark-color);
  font-weight: 700;
  font-size: 16px;
  line-height: 120%;
  color: black;
}