.hero{
  color: var(--light-color);
  background: url('/img/hero_bg.jpg');
  @include bg-cover;
  &__title{
    font-weight: 700;
    font-size: 42px;
    line-height: 110%;
    padding-top: 60px;
    margin-bottom: 35px;
    @include large-tablet{
      padding-top: 150px;
      margin-bottom: 25px;
    }
  }
  &__subtitle{
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 15px;
    @include large-tablet{
      margin-bottom: 10px;
    }
  }
  &__descr{
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    margin-bottom: 63px;
    max-width: 574px;
    @include large-tablet{
      margin-bottom: 35px;
    }
  }
  &__btns{
    display: inline-flex;
    flex-direction: column;
    @include large-tablet{
      margin-bottom: 65px;
    }
  }
  &__btns:first-child{
    margin-bottom: 15px;
  }
  &__nav{
    margin-top: 100px;
    @include large-tablet {
      display: none;
    }
  }
  &__list{
    display: flex;
  }
  &__item{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 190px;
    height: 148px;
    border: 1px solid var(--light-color);
  }
  &__item:hover{
    background-color: var(--hover-color);
  }
  &__icon{
    text-align: center;
    width: 35px;
    height: 25px;
    border: 1px solid var(--light-color);
    margin-bottom: 20px;
  }
}