.footer{
  padding-top: 100px;
  background-color: #585858;
  color: var(--light-color);
  @include large-tablet{
    padding-top: 50px;
  }
  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &-logos{
    flex: 0 1 30%;
    @include large-tablet{
      flex: 0 1 100%;
    }
    &__top:first-child{
      margin-bottom: 10px;
    }
    &__descr{
      font-weight: 400;
      font-size: 10px;
      line-height: 135%;
      max-width: 140px;
      margin-top: 10px;
      margin-bottom: 45px;
    }
    &__partners{
     display: grid;
     grid-template-columns: repeat(2, 1fr);
     grid-gap: 5px;
    }
    &__copy{
  
    }
  }
  &-services{
    flex: 0 1 30%;
    &__title {
      font-weight: 600;
      font-size: 18px;
      line-height: 120%;
      margin-bottom: 20px;
    }
    &__links{
      display: flex;
      flex-direction: column;
      margin-bottom: 145px;
    }
    &__link{
      color: var(--light-color);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
    }
  }
  &-contacts{
    flex: 0 1 30%;
    &__wrapper{
      display: flex;
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      margin-bottom: 47px;
    }
    &__title{
      margin-bottom: 20px;
    }
    &__left{
      margin-right: 40px;
    }
    &__tel{
      display: flex;
      flex-direction: column;
      color: var(--light-color);
      margin-bottom: 10px;
    }
    &__mail{
      color: var(--light-color);
    }
    &__descr{
      margin-top: 12px;
    }
    &__text{
      
    }
    &__social{
      margin-bottom: 45px;
    }
    &__metrick::last-child{
      margin-left: 20px;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    width: 100%;
  }
  &__bottom:not(:last-child){
    margin-right: 100px;
  }
}
.iso, .rez, .dekka, .dmca{
  width: 90px;
  height: 39px;
  fill: var(--light-color);
}
