// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --grid_margin: percentage(40 / 1920); // 40 / 1920 * 100%;
  --grid_width: percentage(126 / (1920 - 40 * 2));
  --grid_gutter: percentage(30 / (1920 - 40 * 2));


  // colors
  --light-color: #fff;
  --dark-color: #333333;
  --grey-color: #f6f6f6;
  --akcent-color: #DC0C1C;
  --hover-color: #141414;
  --burger_menu-color: #141414;

  //burger
  --burger-width: 28px;
  --burger-height: 18px;
  --burger-line-height: 2px;
  --burger-color: var(--akcent-color);

}
