.work{
  padding-bottom: 100px;
  background-color: var(--grey-color);
  @include large-tablet{
    padding-bottom: 50px;
  }
  &__title{
    max-width: 527px;
    margin-bottom: 50px;
    @include large-tablet{
      margin-bottom: 25px;
    }
  }
  &__text{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    max-height: 250px;
    margin-bottom: 60px;
    @include large-tablet{
      flex-wrap: nowrap;
      margin-bottom: 50px;
    }
  }
  &__descr{
    max-width: 440px;
    flex: 1 1 50%;
    font-weight: 400;
    font-size: 16px;
    line-height: 135%;
    @include large-tablet{
      max-width: 1000px;
    }
    &--second{
      margin-top: 15px;
      margin-bottom: 15px;
      @include large-tablet{
        margin-top: 10px;
        margin-bottom: 10px;
      }
    }
    &--last{
      font-weight: 400;
      font-size: 14px;
      line-height: 135%;
    }
    @include large-tablet{
      margin-top: 20px;
    }
  }
  &__watch{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 35px;
  }
  &__watch p{
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
  }
  &__list{
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   align-items: center;
    @include large-tablet{
      margin-top: 130px;
    }
    @include tablet{
      flex-direction: column;
      align-items: start;
    }
    @include small-tablet{
      margin-top: 165px;
    }
    @include mobile{
      margin-top: 185px;
    }
    @media (max-width: 425px){
      margin-top: 300px;
    }
    @media (max-width: 375px){
      margin-top: 375px;
    }
    @media (max-width: 325px){
      margin-top: 425px;
    }

  }
  &__item{
    flex: 0 1 33%;
    border-bottom: 2px solid var(--grey-color);
    padding-bottom: 10px;
    @include desktop{
      flex: 0 1 30%;    
    }
    @include large-tablet{
      flex: 0 1 28%;
    }
    @include tablet{
      flex: 0 1 100%;
    }
  }
  // &__item:after{
  //   content: '';
  //   position: absolute;
  //   display: block;
  //   bottom: -10px;
  //   left: 0;
  //   width: 355px;
  //   height: 1px;
  //   background: #b0b0b0;
  //   @include desctop{
  //     width: 80%;
  //   }
  // }
  &__number {
    font-weight: 400;
    font-size: 48px;
    line-height: 120%;
    color: var(--akcent-color);
    margin-bottom: 10px;
    @include large-tablet{
      font-size: 42px;
    }
  }
  &__article {
    margin-top: 100px;
  }
}
.watch{
  &__wrapper{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    border: 1px solid var(--akcent-color);
    margin-right: 10px;
  }
}
.item__descr{
  font-weight: 500;
  font-size: 18px;
  line-height: 120%;
}
.pt {
  padding-top: 50px;
  @include large-tablet{
    padding-top: 0;
  }
}
.article{
  display: flex;
  justify-content: space-between;
  margin-bottom: 75px;
  @include large-tablet{
    flex-direction: column;
    margin-top: 75px;
  }
  &__title{
    flex: 0 1 50%;
    max-width: 532px;
    @include large-tablet{
      margin-bottom: 15px;
    }
  }
  &__text{
    flex: 0 1 50%;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    @include large-tablet{
      // margin-bottom: 45px;
    }
}
.bottom{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @include tablet{
    flex-direction: column;
  }
  &__item{
    flex: 0 1 33%;
    
  }
  &__number{
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 36px;
    line-height: 120%;
  }
  &__title{
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    margin-bottom: 15px;
  }
  &__descr{
    font-weight: 400;
    font-size: 14px;
    line-height: 135%;
    color: #333333;
    margin-bottom: 35px;
  }
}
}