.faq{
  &__title{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 120%;
    max-width: 500px;
    margin-bottom: 60px;
  }
  &__line{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__list{
    border-top: 1px solid #000;
  }
  &__item{
    border-bottom: 1px solid #000;
  }
  &__question{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    padding: 25px 0;
  }
  &__btn{
    position: relative;
    width: 30px;
    height: 30px;
    transition: .3s ease;
  }
  // &__btn._active> .faq__btn{
  //     background-color: var(--akcent-color);
  // }
  &__btn::after, &__btn::before{
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    height: 3px;
    background: var(--dark-color);
    transition: .3s ease;
  }
  &__btn::after{
    transform: rotate(90deg);
  }
  &__answer{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    max-width: 296px;
    display: none;
    overflow: hidden;
    transition: .3s;
    margin-bottom: 25px;
  }
}
._active{
  background-color: var(--akcent-color);
  border-radius: 100%;
}
._active::before, ._active::after{
  background: var(--light-color);
}
._active::before{
  transform: rotate(45deg);
}
._active::after{
  transform: rotate(-45deg);
}